@import url(https://fonts.googleapis.com/css?family=Barlow+Condensed:400,500,600,700,800,900&display=swap&subset=latin-ext);
html,body{font-family:"Advent Pro", "Kanit", "Barlow Condensed", sans-serif}h1{text-transform:uppercase;font-size:4rem;font-weight:900;letter-spacing:0.75rem}*{padding:0;margin:0;box-sizing:border-box}

.parallax-background{position:fixed;display:flex;flex-wrap:wrap;justify-content:center;align-items:center;width:100vw;height:100vh;background-image:-webkit-gradient(linear, left bottom, left top, from(#011c26), to(#012e40));background-image:linear-gradient(to top, #011c26, #012e40)}.parallax-background__elements{position:absolute !important;left:auto !important;right:0}.parallax-background__element{position:relative;right:-50%;background-color:#6393a6;opacity:0.05;-webkit-transform:rotate(45deg);transform:rotate(45deg);border-radius:10px;--distanceBetweenCircle: 100px}.parallax-background__element--first{top:calc(var(--distanceBetweenCircle) * 6 / -2);width:calc(100vh + var(--distanceBetweenCircle) * 6);height:calc(100vh + var(--distanceBetweenCircle) * 6)}.parallax-background__element--second{top:calc(var(--distanceBetweenCircle) * 5 / -2);width:calc(100vh + var(--distanceBetweenCircle) * 5);height:calc(100vh + var(--distanceBetweenCircle) * 5)}.parallax-background__element--third{top:calc(var(--distanceBetweenCircle) * 4 / -2);width:calc(100vh + var(--distanceBetweenCircle) * 4);height:calc(100vh + var(--distanceBetweenCircle) * 4)}.parallax-background__element--fourth{top:calc(var(--distanceBetweenCircle) * 3 / -2);width:calc(100vh + var(--distanceBetweenCircle) * 3);height:calc(100vh + var(--distanceBetweenCircle) * 3)}.parallax-background__element--fifth{top:calc(var(--distanceBetweenCircle) * 2 / -2);width:calc(100vh + var(--distanceBetweenCircle) * 2);height:calc(100vh + var(--distanceBetweenCircle) * 2)}.parallax-background__element--sixth{top:calc(var(--distanceBetweenCircle) / -2);width:calc(100vh + var(--distanceBetweenCircle));height:calc(100vh + var(--distanceBetweenCircle))}

.header{position:fixed;display:flex;left:0;right:0;height:100vh;z-index:1}.header--active,.header--deactive{z-index:2}.header__hamburger{position:absolute;z-index:1;width:100px;height:100vh;display:flex;justify-content:center;top:25%}.header__hamburger-icon{position:relative;left:0;top:0;width:55px;height:55px;pointer-events:none;-webkit-transition:0.3s;transition:0.3s}.header__hamburger-icon svg{position:absolute;top:-18px;left:-18px;-webkit-transform:scale(0.09);transform:scale(0.09);-webkit-transform-origin:0 0;transform-origin:0 0}.header__hamburger-icon svg path{stroke:#d8e8f2;stroke-width:40px;stroke-linecap:round;stroke-linejoin:round;fill:transparent;-webkit-transition:stroke-dasharray 0.6s;transition:stroke-dasharray 0.6s;-webkit-transition:0.4s;transition:0.4s}.header__hamburger-icon svg path.header__hamburger-path1{stroke-dashoffset:5803.15px;stroke-dasharray:2901.57px, 2981.57px, 240px}.header__hamburger-icon svg path.header__hamburger-path2{stroke-dashoffset:800px;stroke-dasharray:400px, 480px, 240px}.header__hamburger-icon svg path.header__hamburger-path3{stroke-dashoffset:6993.11px;stroke-dasharray:3496.56px, 3576.56px, 240px}.header__hamburger-icon.header__hamburger-active svg path{stroke:#011c26;-webkit-transition:0.4s;transition:0.4s}.header__hamburger-icon.header__hamburger-active svg path.header__hamburger-path1{stroke-dasharray:2901.57px, 5258.15px, 240px}.header__hamburger-icon.header__hamburger-active svg path.header__hamburger-path2{stroke-dasharray:400px, 600px, 0px}.header__hamburger-icon.header__hamburger-active svg path.header__hamburger-path3{stroke-dasharray:3496.56px, 6448.11px, 240px}.header__hamburger-icon-button{position:relative;width:100%;height:100%;cursor:pointer;pointer-events:auto;background:none;border:none;margin:0;padding:0;outline:none}.header__info{width:70%;visibility:hidden}.header__info .header__info-inner{width:100%;height:100%;background-color:#d8e8f2;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:0px 0px;transform-origin:0px 0px;-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.6s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.6s;-webkit-transition-delay:0.1s;transition-delay:0.1s}.header__info--active{visibility:visible}.header__info--active .header__info-inner{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.6s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.6s;-webkit-transition-delay:0.1s;transition-delay:0.1s}.header__empty{width:30%;visibility:hidden}.header__empty .header__empty-inner{width:100%;height:100%;background-color:#d8e8f2;-webkit-transform:scaleX(0);transform:scaleX(0);-webkit-transform-origin:0px 0px;transform-origin:0px 0px;-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;-webkit-transition-delay:0.2s;transition-delay:0.2s}.header__empty--active{visibility:visible}.header__empty--active .header__empty-inner{-webkit-transform:scaleX(1);transform:scaleX(1);-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;-webkit-transition-delay:0.2s;transition-delay:0.2s}.header__empty--first-element{width:100px}.header__empty--first-element .header__empty-inner{-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.4s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.4s}.header__empty--first-element .header__empty--active .header__empty-inner{-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.4s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.4s}

.main-section__element{color:white}

.main-section{position:relative;z-index:1;margin-left:100px}.main-section__element{padding-left:50px}.main-section__element>div{display:flex;align-items:center}#fp-nav{position:absolute;z-index:1;left:37px;bottom:0}#fp-nav>ul{list-style:none}#fp-nav>ul>li{line-height:3px}#fp-nav>ul>li>a{display:inline-block;margin:10px 0;padding:15px 0}#fp-nav>ul>li>a.active>span:nth-child(2){width:50px;-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s}#fp-nav>ul>li>a>span:nth-child(1){display:none}#fp-nav>ul>li>a>span:nth-child(2){display:block;width:25px;height:4px;border-radius:10px;-webkit-transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;transition:cubic-bezier(0.95, 0.65, 0.15, 1) 0.3s;background-color:#d8e8f2}

